<template>
    <div class="m-publish-header">
        <el-page-header @back="goBack" :content="name"></el-page-header>
        <div class="m-publish-store" v-if="localDraft">
            <el-button
                type="primary"
                icon="el-icon-folder"
                @click="openDraftBox"
                ></el-button
            >
            <el-dialog
                class="c-large-dialog"
                title=""
                :visible.sync="dialogVisible"
            >
                <!--  -->
                <el-button
                    class="u-clear"
                    plain
                    icon="el-icon-delete"
                    size="small"
                    @click="clear"
                    ></el-button
                >
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column type="expand">
                        <template slot-scope="props">
                            {{ props.row.content }}
                        </template>
                    </el-table-column>
                    <el-table-column label="" prop="key">
                    </el-table-column>
                    <el-table-column label="" class="u-action">
                        <template slot-scope="scope">
                            <el-button
                                size="mini"
                                @click="useDraft(scope.$index, scope.row.key)"
                                ></el-button
                            >
                            <el-button
                                size="mini"
                                type="danger"
                                @click="
                                    deleteDraft(scope.$index, scope.row.key)
                                "
                                ></el-button
                            >
                        </template>
                    </el-table-column>
                </el-table>

                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="closeDraftBox"
                        > </el-button
                    >
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
const { DB , savePost } = require("../../utils/autoSave");

export default {
    name: "pubheader",
    props: ["name", "localDraft"],
    data: function() {
        return {
            dialogVisible: false,
            selectedCount: 0,
            tableData: [],
        };
    },
    computed: {},
    methods: {
        goBack: function() {
            this.$alert(
                ",",
                "",
                {
                    confirmButtonText: "",
                    callback: (action) => {
                        savePost(this.name, this.$store.state);
                        this.$root.$router.push("/");
                    },
                }
            );
        },
        openDraftBox: function() {
            this.dialogVisible = true;
            this.loadDrafts();
        },
        closeDraftBox: function() {
            this.dialogVisible = false;
        },
        loadDrafts: function() {
            this.tableData = []
            DB.iterate((val, key, i) => {
                this.tableData.push({
                    key: key,
                    content: val.post.post_content,
                });
            })
                .then(function() {
                    console.log("[IDB] Iteration has completed");
                })
                .catch(function(err) {
                    console.log(err);
                });
        },
        clear: function() {
            this.$alert("", "", {
                confirmButtonText: "",
                callback: (action) => {
                    DB.clear();
                    this.tableData = [];
                },
            });
        },
        useDraft: function(i, key) {
            DB.getItem(key).then((data) => {
                this.$store.replaceState(data)
                this.closeDraftBox();
            });
        },
        deleteDraft: function(i, key) {
            this.tableData.splice(i, 1);
            DB.removeItem(key);
        },
    },
    mounted: function() {},
    components: {},
};
</script>

